

<template>
    <div class="loginVessel">
        <!-- 顶部导航 -->
        <van-nav-bar class="loginBar" :border='false'>
               
               <template #title>
                   <span class="title">
                          账户登录
                   </span>
               </template>

               <template #right>
                   <div class="navigation">
                       <span class="iconfont icon-sangedian" ></span>
                       <span class="iconfont icon-tongxinyuan" @click="getPage('Home')"></span>
                   </div>
               </template>
        </van-nav-bar>

        <!-- logo图标 -->
        <div  class="iconfont icon-icon_ruixingkafei1 logins"></div>
        <p><span></span></p>
        

        <!-- 登录表单 -->
        <van-form  > 
            <!-- 输入框 -->
            <van-field
                v-model="oldObj.phone"
                class="froms01"
                placeholder="手机号"
            />
            <van-field
                v-model="oldObj.password"
                class="froms02"
                :type="eye01 ? 'text' : 'password'"
                placeholder="密码"
                :right-icon="eye01 ? 'eye-o'  : 'closed-eye'"
                @click-right-icon="isTag" 
                
            />
           
             <!-- 忘记密码/注册 -->
            <div class="txTiao">
                <span><van-cell class="vanCell" title="注册帐号"   @click="showPopup">
                     
                </van-cell></span>
                <span @click="getPage('Findack')">忘记密码?</span>
                
         
            </div>



           <!-- 登录键 -->
            <div style="margin: 20px;">
                <van-button round block type="info" native-type="submit" :color="isColor ? '#4e6eb0' :  '#bfc9e2'" @click='ff_login'>
                登录
                </van-button>
            </div>
           <!-- 条例 -->
           <div class="rules">点击确定,即表示已阅读并同意<a href="#">《注册会员服务条例》</a></div>

        </van-form>

        

        <!-- 注册 -->
        <van-popup 
                  v-model="show" 
                  round  
                  position="bottom" 
                  closeable
                  close-icon="close"
                  :style="{ height: '45%' }" >

                  
              <div class="tiles">注册</div>
              <van-form validate-first  class="fromTest">
                    <!-- 通过 pattern 进行正则校验 -->
                    <van-field
                        v-model="newObj.phone" 
                        label="手机号"
                        placeholder="11位手机号"
                    />
                    <!-- 通过 validator 进行函数校验 -->
                    <van-field
                        v-model="newObj.nickName" 
                        label="昵称"
                        placeholder="1-10位昵称"  
                    />
                    <!-- 通过 validator 进行异步函数校验 -->
                    <van-field
                        v-model="newObj.password" 
                        label="密码"
                        :type="eye02 ? 'text' : 'password'"
                        placeholder="6-16位密码且以字母开头"  
                        :right-icon="eye02 ? 'eye-o'  : 'closed-eye'"
                        @click-right-icon="isTag02" 
                    />
                      <!-- <template #button>
                        <van-icon :name="eye02 ? 'eye-o'  : 'closed-eye'" class="eyes02" @click.stop="isTag02"></van-icon>
                      </template> -->
                   
                    
                  
                    <div style="margin: 16px;">
                        <van-button color="#6d87c4" round block type="info" @click="ff_enroll">
                        注册
                        </van-button>
                    </div>
             </van-form>
        </van-popup>
        

       
    </div>
</template>

<script>
   import '../assets/less/login.less'
   import '../assets/font/iconfont.css'

   import {inspectFrom} from '../assets/js/inspectFrom/inspectFrom'

    export default {
        name:'Login',
         data() {
            return {
                // 注册与登录的密码框切换
                eye01:false,
                eye02:false,

                // 下弹出注册层
                show: false,

                // 老用户登录
                oldObj:{
                    phone:null,
                    password:null,
                },
                // 新用户注册
                newObj:{
                    phone:null,
                    nickName:null,
                    password:null
                },
                isColor : false
                };
            },

    methods: {
        // 弹出注册层的方法
        showPopup() {
        this.show = true;
        },

        // 登录注册密码的闭眼的方法
        isTag(){
            this.eye01 =  !this.eye01;
        },
        isTag02(){
            this.eye02 =  !this.eye02;
        },

        // 注册的方法
        ff_enroll(){
            let obj = {
                phone:{
                    value    :  this.newObj.phone,
                    errorMsg :  '手机号格式错误',
                    reg      :  /^1[3-9]\d{9}$/
                },
                nickName:{
                    value    :  this.newObj.nickName,
                    errorMsg :  '昵称格式错误',
                    reg      :  /^[\w\u4e00-\u9fa5]{1,10}$/
                },
                password:{
                    value    :  this.newObj.password,
                    errorMsg :  '密码格式错误',
                    reg      :   /^[A-Za-z]\w{5,15}$/
                },
            }
           let isTest = inspectFrom.inspect(obj);
          


           if(isTest){
                let useObj = {};
                Object.assign(useObj,this.newObj);
                  
                useObj.appkey = this.appkey;
                this.$toast.loading({
                                message: '加载中...',
                                // 禁止点击背景
                                forbidClick: true,
                                loadingType: 'spinner',
                    });
               
               
               
               


                this.axios({
                    url:'/register',
                    method:'POST',
                    data:useObj
                })
                .then(res => {
                    // 接收后台的响应
                       if(res.data.code == 100){

                           this.$toast(res.data.msg);
                           setTimeout(()=>{
                                this.show = false;
                           },700)
                       }else{
                           this.$toast(res.data.msg)
                       }
                })
                .catch(err =>{

                })
           }

        },

        // 登录的方法
        ff_login(){
               let obj = {
                phone:{
                    value    :  this.oldObj.phone,
                    errorMsg :  '手机号格式错误',
                    reg      :  /^1[3-9]\d{9}$/
                },
                password:{
                    value    :  this.oldObj.password,
                    errorMsg :  '密码格式错误',
                    reg      :  /^[A-Za-z]\w{5,15}$/
                },
            }
           let isTest = inspectFrom.inspect(obj);
            if(isTest){
                let useObj = {};
                Object.assign(useObj,this.oldObj);
                useObj.appkey = this.appkey;
                this.$toast.loading({
                        message: '加载中...',
                        // 禁止点击背景
                        forbidClick: true,
                        loadingType: 'spinner',
                    });


                this.axios({
                    url:'/login',
                    method:'POST',
                    data:useObj
                })
                .then(res => {
                     
                    // 接收后台的响应
                       if(res.data.code == 200){
                           this.$toast(res.data.msg);

                           // token为后台生成的唯一用户标识符 为用户id
                           window.localStorage.setItem('useId',res.data.token);
                           window.sessionStorage.setItem('passWord',this.oldObj.password);

                           setTimeout(()=>{
                                 
                                 this.$router.push({name:'Home'})
                           },500)
                       }else{
                           this.$toast(res.data.msg)
                       }
                })
                .catch(err =>{

                })
           }

        },

        getBack(){
                 this.$router.go(-1);
        },
        getPage(name){
                 this.$router.push({name})
        }
    },
    updated() {
       if(this.oldObj.phone.length == 11 && this.oldObj.password.length >=5){
             this.isColor = true;
       }else{
             this.isColor = false;
       }
    },
    }
</script>

<style lang="less" scoped>

</style>